export default {
	data() {
		return {
			height: 64, //header高度
			top: 0, //标题图标距离顶部距离
			scrollH: 0, //滚动总高度
			opcity: 0,
			iconOpcity: 0,
			stepsList:[],
			day:0,
			current:-1,
			punchShow:false,
			myPoint:0,
			getPoint:'',
			canSign:true
		};
	},
	methods:{
		// 查看积分明细
		handleToCheckIntegral(){
			uni.navigateTo({
				url:'../My/integral'
			})
		},
		// 返回
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		// 签到
		async handleToPunch(){
			if(!this.canSign) return
			let res = await this.$fetch(this.$api.addSign,{},'GET')
			console.log(res);
			if(res.code == 0){
				this.punchShow = true
				this.day++;
				this.current++;
				this.canSign = false
			}
		},
		// 获取签到信息
		async getSign(){
			let res = await this.$fetch(this.$api.getSign,{},'GET')
			console.log(res);
			this.canSign = res.data.canSign
			if(res.data.sign.amount  == 0){
				var today = new Date()
				var i
				var signAmount=res.data.sign.amount
				this.day = signAmount
				if(res.data.canSign){
					this.current= signAmount-1
				}else{
					this.current = signAmount
				}
				for (i = 0; i < 7; i++) {  
					var now = new Date()
					now.setDate(today.getDate() + i-signAmount)
					var MM =now.getMonth() + 1 < 10? '0' + (now.getMonth() + 1): now.getMonth() + 1
					var Day =now.getDate() < 10 ? '0' + now.getDate() : now.getDate()
					if(i == signAmount){
						this.stepsList.push({name:'今天'})
					}
					else{
						this.stepsList.push({name:MM+'.'+Day})
					}
				    
				}  
			}else if(res.data.sign.amount > 0 && res.data.sign.amount <= 6){
				var today = new Date()
				var i
				var signAmount=res.data.sign.amount
				this.day = signAmount
				if(res.data.canSign){
					this.current= signAmount-1
					for (i = 0; i < 7; i++) {
						var now = new Date()
						now.setDate(today.getDate() + i-signAmount)
						var MM =now.getMonth() + 1 < 10? '0' + (now.getMonth() + 1): now.getMonth() + 1
						var Day =now.getDate() < 10 ? '0' + now.getDate() : now.getDate()
						if(i == signAmount){
							this.stepsList.push({name:'今天'})
						}else{
							this.stepsList.push({name:MM+'.'+Day})
						}
					    
					}
				}else{
					this.current = signAmount - 1
					let signIndex = signAmount -1
					for (i = 0; i < 7; i++) {
						var now = new Date()
						now.setDate(today.getDate() + i - signIndex)
						var MM =now.getMonth() + 1 < 10? '0' + (now.getMonth() + 1): now.getMonth() + 1
						var Day =now.getDate() < 10 ? '0' + now.getDate() : now.getDate()
						if(i == signIndex){
							this.stepsList.push({name:'今天'})
						}else{
							this.stepsList.push({name:MM+'.'+Day})
						}
					}
					console.log(this.current);
				}
			}else if(res.data.amount >= 7){
				var today = new Date()
				var i
				var signAmount=res.data.sign.amount
				this.day = signAmount
				if(res.data.canSign){
					this.current = 5
					for (i = 0; i < 7; i++) {
						var now = new Date()
						now.setDate(today.getDate() + i-6)
						var MM =now.getMonth() + 1 < 10? '0' + (now.getMonth() + 1): now.getMonth() + 1
						var Day =now.getDate() < 10 ? '0' + now.getDate() : now.getDate()
						if(i == 6){
							this.stepsList.push({name:'今天'})
						}else{
							this.stepsList.push({name:MM+'.'+Day})
						}
					    
					}
				}else{
					this.current == 6
					for (i = 0; i < 7; i++) {
						var now = new Date()
						now.setDate(today.getDate() + i-6)
						var MM =now.getMonth() + 1 < 10? '0' + (now.getMonth() + 1): now.getMonth() + 1
						var Day =now.getDate() < 10 ? '0' + now.getDate() : now.getDate()
						if(i == 6){
							this.stepsList.push({name:'今天'})
						}else{
							this.stepsList.push({name:MM+'.'+Day})
						}
					    
					}
				}
			}
		}
	},
	onLoad(options) {
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();







		setTimeout(() => {
			uni.getSystemInfo({
				success: (res) => {
					this.width = obj.left || res.windowWidth;
					this.height = obj.top ? (obj.top + obj.height + 8) : (res.statusBarHeight + 44);
					this.top = obj.top ? (obj.top + (obj.height - 32) / 2) : (res.statusBarHeight + 6);
					this.scrollH = res.windowWidth
				}
			})
		}, 50)
		this.myPoint = options.point
		this.getSign()
	},
	onPageScroll(e) {
		let scroll = e.scrollTop <= 0 ? 0 : e.scrollTop;
		let opcity = scroll / this.scrollH;
		if (this.opcity >= 1 && opcity >= 1) {
			return;
		}
		this.opcity = opcity;
		// this.iconOpcity = 0.5 * (1 - opcity < 0 ? 0 : 1 - opcity)
	},
}
